import { createRouter, createWebHistory } from "vue-router";
import IndexView from "../views/IndexView.vue";
import UserTest from "../views/UserTest.vue";

const routes = [
  {
    path: "/",
    component: IndexView,
    children: [
      {
        path: "",
        component: () => import("../views/HomeView"),
        name: "home",
      },
      {
        path: "products",
        component: () => import("../components/product/ProductList"),
        name: "products",
      },
      {
        path: "about",
        component: () => import("../components/HelloWorld"),
        name: "about",
      },
      {
        path: "detail/:productID",
        component: () => import("../components/product/ProductDetail"),
        name: "detail",
      },
    ],
  },
  {
    path: "/test",
    // component: () => import("../components/product/ProductDetail"),
    component: () => import("../components/HeaderComponent"),
    name: "test",
  },
  {
    path: "/test2",
    // component: () => import("../components/product/ProductDetail"),
    component: () => import("../components/Test2Component"),
    name: "test2",
  },
  {
    // 未知路由導向
    path: "/:pathMatch(.*)*",
    name: "ReduceHome",
    redirect: { name: "home" },
  },
  {
    path: "/product",
    // name: "productView",
  },
  {
    path: "/about2",
    // name: "about",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/AboutView.vue"),
  },
  {
    path: "/users/:userId",
    component: UserTest,
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
