<template>
  <div class="test">
    <div class="background-image">
      <div class="header">
        <img class="logo" src="../assets/Logo_去背.png" />
      </div>
      <div class="overlay"></div>
      <!-- <div class="content">logo</div> -->
    </div>
  </div>
</template>

<script>
// document.addEventListener("scroll", function () {
//   var scrollTop = window.scrollY;
//   var overlay = document.querySelector(".overlay");
//   var opacity = Math.min(1, scrollTop / (window.innerHeight / 2));
//   overlay.style.opacity = opacity;
// });
export default {
  data() {
    return {
      scrollY: 0,
      headerFixed: false,
      logo: require("../assets/Logo_去背.png"),
      initialImageScale: 1,
      minImageScale: 0.5,
      scaleThreshold: 200,
    };
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  methods: {
    handleScroll() {
      this.scrollY = window.scrollY;
      // if (this.scrollY > 200) {
      //   // 調整200為您希望固定 header 的滾動位置
      //   this.headerFixed = true;
      // } else {
      //   this.headerFixed = false;
      // }
      // this.backgroundImageStyle.backgroundPositionY = -this.scrollY * 1 + "px"; // 調整滾動速度

      var scrollTop = window.scrollY;
/*
      // 选择 overlay 元素并设置其不透明度
          const overlay = document.querySelector(".overlay");
          if (overlay) {
            const opacity = Math.min(1, scrollTop / (window.innerHeight / 2));
            overlay.style.opacity = opacity;
          }

          // 选择 header 元素并设置其位置和高度
          const header = document.querySelector(".header");
          if (header) {
            if (this.scrollY > window.innerHeight / 2 - window.innerHeight / 10) {
              header.style.position = "sticky";
              header.style.height = "20%";
            } else {
              header.style.position = "absolute";
              header.style.height = null;
            }
          }
*/
      var overlay = document.querySelector(".overlay");
      var opacity = Math.min(1, scrollTop / (window.innerHeight / 2));
      overlay.style.opacity = opacity;
      
      var header = document.querySelector(".header");
      if (header) {
        const scale = Math.max(
          this.minImageScale,
          this.initialImageScale - (this.scrollY / this.scaleThreshold) * (this.initialImageScale - this.minImageScale)
        );
        header.style.transform = `scale(${scale})`
      }
      if (this.scrollY > window.innerHeight / 2 - window.innerHeight / 10) {
        header.style.position = "sticky";
        header.style.height = "100%";
      } else {
        header.style.position = "absolute";
        header.style.height = null;
      }
    },
  },
};
</script>

<style scoped>
.logo {
  max-width: 100%;
  max-height: 100%;
  display: inline-block;
}
.header {
  position: absolute;
  top: 10%;
  right: 10%;
  bottom: 10%;
  left: 10%;
  margin: auto;
  /* height: 20%;
  width: 15%; */
  display: inline-block;
  align-items: center;
  text-align: center;
  z-index: 2;
}
.background-image {
  background-image: url("../picture/test.jpg");
  background-size: cover;
  background-position: center;
  height: 100vh;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  /* padding: 20%; */
  /* min-height: 600px; */
}

.overlay {
  position: absolute;
  opacity: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    to bottom,
    rgba(255, 255, 255, 0) 0%,
    rgba(255, 255, 255, 1) 100%
  );
}

.content {
  position: absolute;
  top: 100vh;
  right: 0;
  bottom: 0;
  left: 0;
  height: 80vh;
  z-index: 1;
  overflow: scroll;
  /* 其他樣式 */
}
</style>
